import forumobjct from "../../script/forumobjct";
import node_list from './list';

export default {
  name: "list.dialog",
  mixins: [forumobjct],
  components: {
    node_list
  },
  props: {
    show: undefined,
    permission_check: undefined,
    dialog_title: undefined
  },
  watch: {
    show: function (newValue, oldValue) {
      this.$emit('update:show', newValue);
    }
  },
  data() {
    return {}
  },
  created() {
    if (!this.dialog_title) {
      this.dialog_title = this.$l('thread.all_node');
    }
  },
  methods: {
    on_hide: function () {
      this.show = false;
    },
    on_select: function (forum_conf) {
      console.log('[on_select]forum_conf:', forum_conf);
      this.$emit('on_select_forum', forum_conf);
    }
  }
}
