import Vue from "vue";
import USERVISIT from '@/modules/vip'

export default {
  name: "userobject",
  data() {
    return {}
  },
  methods: {
    update_user_info: function () {
      this.$axios.get('/app/user/info', {
        params: {}
      }).then((response) => {
        var message_notify = response.data.message_notify;
        if (message_notify) {
          var num = parseInt(message_notify.num);
        }
        console.log("message_notify:", message_notify);
        this.message_notify = message_notify;
        if (this.message_notify.num <= 0) {
          this.message_notify.num = undefined;
        }
      }).catch(function (error) {

      });
    },
    is_login: function () {
      return this.userdata && this.userdata.username;
    },
    buy_discount: function () {
      if (this.is_login()) {
        var vip_degree = this.$vip_degree_conf[this.userdata.vip_degree];
        if (vip_degree) {
          return vip_degree;
        }
      }
    },
    this_vip_degree(userdata) {
      console.log('[this_vip_degree]:', userdata);
      var vip_conf = undefined;
      if (this.$vip_degree_conf && userdata) {
        vip_conf = this.$vip_degree_conf[userdata.vip_degree];
      }
      if (!vip_conf) {
        vip_conf = {
          "name": "未开通VIP",
          "discount": 1,
          'degree': "vip0"
        };
      }
      return vip_conf
    },
    update_userinfo: function (userdata) {
      userdata.is_vip = false;
      if (userdata.vip && userdata.vip > (new Date().getTime()) / 1000) {
        userdata.is_vip = true;
      } else {
        userdata.vip_degree = 'vip0';
      }
      if (!userdata.gold) {
        userdata.gold = 0;
      }
      if (!userdata.user_degree) {
        userdata.user_degree = 'ud10';
      }
      userdata.is_manager = Vue.prototype.$config.manager_user_ids.indexOf(userdata.uid) >= 0;
      if (userdata.is_manager) {
        userdata.user_degree = 'admin';
      }
      userdata.user_visit = new USERVISIT(userdata.uid);
      console.log("[update_userinfo]", userdata);
      return userdata
    },
    user_degree_conf: function (userdata) {
      if (!userdata.degree) {
        userdata.degree = 'ud10';
      }
      var udc = this.$user_degree_conf[userdata.degree];
      return Object.assign({}, udc);
    },
    get_userdata: function ({uid = undefined, cb = undefined}) {
      this.$axios.get('/app/userdata/' + uid, {
        params: {}
      }).then((response) => {
        console.log('[get_userdata]:', response);
        if (cb) {
          cb(response);
        }
      }).catch(function (error) {

      });
    }
  },
  created: function () {
    this.userdata = this.$store.state.user.userinfo;
    console.log("userobject created:", this.userdata);
    if (!this.userdata)
      this.userdata = {};
    this.update_userinfo(this.userdata);
  }
}
