import forumobjct from "../../script/forumobjct";

export default {
  name: "list",
  mixins: [forumobjct],
  props: {
    show_index: undefined
  },
  data() {
    return {
      forums_options: []
    }
  },
  created() {
    this.update_forums_options((this.$forum_conf.forums), this.forums_options);
    console.log('forums_options:', this.forums_options);
    var new_forums_options = [];
    for (let i = 0; i < this.forums_options.length; ++i) {
      var forums_option = this.forums_options[i];
      var add_forum = this.is_add_forum(forums_option);
      if (add_forum) {
        new_forums_options.push(forums_option);
      }
    }
    console.log('new_forums_options:', new_forums_options);
    this.forums_options = new_forums_options;
  },
  methods: {
    on_select: function (forum_conf) {
      console.log('[on_select]forum_conf:', forum_conf);
      this.$emit('on_select_forum', forum_conf);
    },
    is_add_forum: function (forums_option) {
      var add_forum = true;
      var permision = forums_option['permision'];
      if (permision) {
        if (permision['userdegree']) {
          if (!this.$store.state.hasLogin || permision['userdegree'].indexOf(this.$store.state.user.userinfo.user_degree) < 0) {
            add_forum = false;
          }
        }
      }
      return add_forum;
    }
  }
}
