import Vue from "vue";


Vue.component("empty_view", {
    template: '<div></div>'
})


Vue.component('remote-res', {
    data() {
        return {
            loaded: false
        };
    },
    render(createElement) {
        let scs = [];
        if (this.css) {
            for (let i = 0; i < this.css.length; i++) {
                let pps = {
                    attrs: {
                        rel: 'stylesheet',
                        href: this.css[i]
                    }
                };
                scs.push(createElement('link', pps));
            }
        }

        if (this.vif && !this.vif()) {
            if (!this.loaded) {
                this.loaded = true;
                if (this.scripts) {
                    for (let i = 0; i < this.scripts.length; i++) {
                        let pps = {
                            attrs: {
                                type: 'text/javascript',
                                src: this.scripts[i]
                            }
                        };
                        if (i === this.scripts.length - 1) {
                            pps.on = {
                                load: () => {
                                    this.$emit("loaded");
                                }
                            };
                        }
                        scs.push(createElement('script', pps));
                    }
                }
            }
        } else {
            if (!this.loaded) {
                this.loaded = true;
                this.$emit("loaded");
            }
        }
        return createElement('div', scs);
    },
    props: {
        vif: {
            type: Function,
            required: true
        },
        scripts: {
            type: Array
        },
        css: {
            type: Array
        },
    },
});

Vue.component('remote-css', {
    render: function (createElement) {
        var self = this;
        return createElement('link', {
            attrs: {
                ref: 'stylesheet',
                href: this.href
            },
            on: {
                load: function (event) {
                    self.$emit('load', event);
                },
                error: function (event) {
                    self.$emit('error', event);
                },
                readystatechange: function (event) {
                    if (this.readyState == 'complete') {
                        self.$emit('load', event);
                    }
                }
            }
        });
    },
    props: {
        href: {
            type: String,
            required: true
        }
    }
});

Vue.component('remote-script', {
    render: function (createElement) {
        var self = this;
        return createElement('script', {
            attrs: {
                type: 'text/javascript',
                src: this.src
            },
            on: {
                load: function (event) {
                    self.$emit('load', event);
                },
                error: function (event) {
                    self.$emit('error', event);
                },
                readystatechange: function (event) {
                    if (this.readyState == 'complete') {
                        self.$emit('load', event);
                    }
                }
            }
        });
    },
    props: {
        src: {
            type: String,
            required: true
        }
    }
});

