Vue.prototype.image_hosts_conf = image_hosts_conf
Vue.prototype.$buy_gold_conf = buy_gold_conf
Vue.prototype.$vip_degree_conf = vip_degree_conf
Vue.prototype.$manhua_conf = manhua_conf
Vue.prototype.$user_degree_conf = user_degree_conf


window.xx = Vue.prototype;