exports.install = function (Vue, options) {

    Vue.prototype.go = function (url) {
        console.log("go:", url);
        if (url == 'refresh') {
            location.reload();
        } else {
            var reg = /(http|https):\/\/([\w.]+\/?)\S*/ig;
            if (url.match(reg)) {
                this.navigateToUrl(url);
            } else {
                this.$router.push(url)
            }
        }
        return false;
    };
    Vue.prototype.init = function () {

        Vue.prototype.init_data = {
            'now_time': (new Date().getTime()) / 1000
        }
        var $web_conf = this.$web_conf;
        var cn_tmp_hosts = $web_conf['cn_tmp_hosts'];
        this.$web_conf.set_host = cn_tmp_hosts[0];
        this.$web_conf.lang = 'chinese';

        this.load_lang();
        if (this.create_axios) {
            this.create_axios();
        }
        if (this.init_traffic) {
            this.init_traffic();
        }
        this.update_row_count();
        this.$axios.get("/app/forum/init").then((response) => {
            this.$store.commit('init', {
                'data': response.data,
                'time': new Date().valueOf()
            });
        })
        this.update_user_info();
    };

    Vue.prototype.set_title = function (t) {
        setTimeout(() => {
            document.title = t + "  -- " + this.$web_conf.application_name;
        }, 50)
    };

    Vue.prototype.get_tracker = function () {
        if ("ga" in window && window.ga.getAll) {
            var trackers = window.ga.getAll();
            if (trackers && trackers.length > 0) {
                return trackers[0];
            }
        }
    };

    Vue.prototype.goto_anchor = function (cb) {
        if (location.hash) {
            setTimeout(() => {
                var e = document.querySelector(location.hash);
                if (e) {
                    e.scrollIntoView();
                    console.log('[goto_anchor]:', window.scrollY);
                    window.scroll(0, window.scrollY - 100);
                }
                if (cb) {
                    cb();
                }
            }, 1);
        }
    };

    Vue.prototype.update_row_count = function () {
        var ROW_COUNT = parseInt(window.innerWidth / 260);
        ROW_COUNT = ROW_COUNT < 3 ? 3 : ROW_COUNT;
        ROW_COUNT = ROW_COUNT > 8 ? 8 : ROW_COUNT;
        Vue.prototype._g.COMMON_ROW_COUNT = ROW_COUNT;
    };

    Vue.prototype.is_mobile = function () {
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
        return flag;
    };

    Vue.prototype.is_ios = function () {
        return [
                'iPad Simulator',
                'iPhone Simulator',
                'iPod Simulator',
                'iPad',
                'iPhone',
                'iPod'
            ].includes(navigator.platform)
            // iPad on iOS 13 detection
            || (navigator.userAgent.includes("Mac") && "ontouchend" in document);
    };

    Vue.prototype.update_url_param = function (update_params) {
        var url_vars = this.getUrlVars();
        var pathname = location.pathname;
        for (let k in url_vars) {
            pathname = this.updateQueryStringParameter(pathname, k, url_vars[k])
        }
        for (let k in update_params) {
            pathname = this.updateQueryStringParameter(pathname, k, update_params[k])
        }
        return pathname;
    };

    Vue.prototype.from_cache = function () {
        var urlvars = this.getUrlVars();
        var from_cache = !this.$web_conf.debug;
        if (urlvars.from_cache) {
            from_cache = JSON.parse(urlvars.from_cache);
        }
        return from_cache;
    }

    Vue.prototype.formatNumber = function (value) {
        value = value.toString();
        if (value.length <= 3) {
            return value;
        } else {
            return this.formatNumber(value.substr(0, value.length - 3)) + ',' + value.substr(value.length - 3);
        }
    };

    Vue.prototype.update_user_info = function () {
        this.$axios.get('/app/user/info', {
            params: {}
        }).then((response) => {
            console.log("user info:", response)
            var login = response.data.login;
            if (!login) {
                //未登录
                this.$store.commit('loginout');
                console.log("token无效或过期")
                return;
            }
            var message_notify = response.data.message_notify;
            if (message_notify) {
                var num = parseInt(message_notify.num);
            }
            console.log("message_notify:", message_notify);
            this.message_notify = message_notify;
            if (this.message_notify.num <= 0) {
                this.message_notify.num = undefined;
            }

            var user = response.data.user;
            if (user) {
                console.log("update user:", user);
                this.$store.commit('login', {
                    'userinfo': user,
                    'token': user.token
                });
            }
        }).catch(function (error) {

        });
    }
};

