export default class USERVISIT {

    constructor(uid) {
        this.uid = uid;
    }

    key_list_key = "look:list";
    last_look_key = 'last:look'

    is_expire = function () {
        var last_look_time = localStorage.getItem(this.last_look_key);
        if (last_look_time) {
            var dt = Date.parse(new Date()) - parseInt(last_look_time);
            console.log('[is_expire]dt:',dt);
            if (dt > 60 * 60 * 16 * 1000) {
                localStorage.removeItem(this.key_list_key);
                return [];
            }
        }
    }

    push = function (id) {
        var look_list = localStorage.getItem(this.key_list_key);
        if (!look_list) {
            look_list = [];
        } else {
            look_list = JSON.parse(look_list);
        }
        look_list.push((id));
        look_list = Array.from(new Set(look_list));
        console.log('look_list:', look_list);
        localStorage.setItem(this.key_list_key, JSON.stringify(look_list));
        localStorage.setItem(this.last_look_key, Date.parse(new Date()));
        return look_list;
    }

    get_look_list = function () {
        var look_list = localStorage.getItem(this.key_list_key);
        console.log('look_list:', look_list);
        if (!look_list) {
            look_list = [];
        } else {
            look_list = JSON.parse(look_list);
        }
        return look_list;
    }

}
