export default {
  name: "forumobjct",
  data() {
    return {}
  },
  methods: {
    get_tabinfo: function ({id, cb, from_cache = true}) {
      this.$axios.get('/app/topic/tabinfo', {
        params: {
          id: id,
          type: "topic",
          from_cache: from_cache
        }
      }).then((response) => {
        var tabinfo = response.data;
        if (cb) {
          cb(tabinfo);
        }
      }).catch(function (error) {
      });
    },
    get_topic: function ({id, cb, edit = false, from_cache = true}) {
      var params = {
        id: id,
        type: "topic",
        from_cache: from_cache
      };
      if (edit) {
        params['edit'] = edit;
      }
      this.$axios.get('/app/release/thread', {
        params: params
      }).then((response) => {
        var edit = response.data.edit;
        var thread = response.data.thread;
        if (cb) {
          cb(thread,edit);
        }
      }).catch(function (error) {
      });
    },
    update_forums_options: function (forum_list, add_to_items) {
      var fid_list = Object.keys(forum_list);
      for (var i = 0; i < fid_list.length; ++i) {
        var fid = fid_list[i];
        var fourm_item = Object.assign({}, forum_list[fid]);
        if (!fourm_item.fid) {
          fourm_item['fid'] = fid;
        }
        var tmp_forum_list = (forum_list[fid]['list']);
        if (tmp_forum_list) {
          tmp_forum_list = Object.assign({}, tmp_forum_list);
          fourm_item['list'] = [];
          this.update_forums_options(tmp_forum_list, fourm_item['list']);
        }
        add_to_items.push(fourm_item);
      }
    },
    //获取对应的一条的节点链
    get_forum_list: function (fid, forum_list) {
      var forum_conf = this.$forum_optimization_conf[fid]
      if (forum_conf) {
        forum_conf['fid'] = fid
        let parent_fid = forum_conf['parent_fid']
        if (parent_fid) {
          var tmp_forum_conf = this.$forum_optimization_conf[parent_fid]
          if (tmp_forum_conf) {
            this.get_forum_list(parent_fid, forum_list)
          }
        }
        forum_list.push(forum_conf)
      }
    },
    get_forum: function (fid) {
      return this.get_node(fid);
    },
    get_node: function (fid) {
      if (this.$forum_optimization_conf[fid]) {
        return Object.assign({}, this.$forum_optimization_conf[fid]);
      }
      return undefined;
    },
    get_topic_list: function ({start, limit, uid, fid = undefined, type = 'released', settotop = undefined, settoessence = undefined, from_cache = true, cb}) {
      var request_params = {
        'start': start,
        'limit': limit,
        'type': type,
        'from_cache': from_cache,
      }
      if (settotop != undefined) {
        request_params['settotop'] = settotop;
      }
      if (settoessence != undefined) {
        request_params['settoessence'] = settoessence;
      }
      if (fid) {
        request_params['fid'] = fid;
      }
      if (uid) {
        request_params['uid'] = uid;
      }
      this.$axios.get("/app/topic/list", {
        params: request_params
      }).then((response) => {
        if (cb) {
          cb(response);
        }
      }).catch((error) => {

      });
    },
    is_make_torrent: function () {
      var mktorrent = this.topic_data.mktorrent;
      if (mktorrent && mktorrent.torrent_id) {
        return "/download/torrent/" + this.topic_data.id;
      }
      if (this.topic_data.video_info) {
        var mktorrents = this.topic_data.video_info.mktorrents;
        for (var i = 0; i < mktorrents.length; ++i) {

        }
      }
    },
    get_torrent_urls: function (topic_data) {
      var mktorrent = this.topic_data.mktorrent;
      if (mktorrent && mktorrent.torrent_id) {
        return "/download/torrent/" + this.topic_data.id;
      }
      if (this.topic_data.video_info) {
        var mktorrents = this.topic_data.video_info.mktorrents;
        for (var i = 0; i < mktorrents.length; ++i) {

        }
      }
    },
    screen_shots_list: function (topic_data) {
      var full_list = [];
      var online_video = topic_data.video_info.online_videos[0];
      var list = online_video.screenshots.list;
      var hash_id = online_video.hash_id;
      for (var i = 0; i < list.length; ++i) {
        var full_url = this.geenrate_screenshot_url(online_video.space_name, hash_id, list[i]);
        full_list.push(full_url);
      }
      console.log('full_list:', full_list);
      return full_list
    },
    load_topic_view_history: function ({start, limit, uid, cb}) {
      var request_params = {
        start: start,
        limit: limit,
        uid: uid
      }
      this.$axios.get("/app/topic/view/history", {
        params: request_params
      }).then((response) => {
        if (cb) {
          cb(response);
        }
      }).catch((error) => {

      });
    }
  },
}
